import Axios from 'axios'
import { getCookie } from '../commonjs/util.js'
Axios.defaults.baseURL = 'http://memberapi.apftown.com'; //
Axios.defaults.withCredentials = true; //ajaxcookie
Axios.defaults.headers.get['Content-Type'] = 'application/x-www-form-urlencoded;text/html;charset=UTF-8'; //
Axios.defaults.headers['APF_UID'] = getCookie("APF_UID")

var ENV = "prod"; // ,dev release,masterprod

export default {
    pathUrl: function() {
        var URLS = {
            dev: {
                MJK: '/apis', //2.0
                UPLOAD: '/upload', //
                MC: '/mc1', //1.0
                WEB_URL: {
                    WEB_MIAO: 'http://shw.miningcircle.com'
                }
            },
            prod: {
                MJK: 'http://memberapi.miningcircle.com', //2.0
                MC: 'http://www.miningcircle.com', //1.0
                APF: 'http://www.apftown.com', //
                NEW: 'http://memberapi.apftown.com:8070',
                UPLOAD: 'http://www.miningcircle.com', //
            },
            test: {
                MC: 'http://www.miningcircle.com', //1.0
                APF: 'http://szj.apftown.com:8888', //
                NEW: 'http://memberapi.apftown.com:8088',
                MJK: 'http://memberapi.miningcircle.com', //2.0
                UPLOAD: 'http://www.miningcircle.com', //
            }
        }
        return URLS[ENV];
    }
}


