<template>
	<div>
		<i-header :index="index"></i-header>
		<div class="top_banner por">
			<img src="../../../static/img/banner_detail/banner3_detail.jpg" />
		</div>
		<div class="trade">
			<div class="container">
				<div class="conBox">
					<div class="con">
						<div class="row col-33">
							<div class="col-md-12 text-ind fs-16 text">
								2018411
							</div>
							<div class="col-md-12 text-ind fs-16 text">
								9
							</div>
							<div class="col-md-12 text-ind fs-16 text">
								 
							</div>
						</div>						
					</div>
					<div class="base">
						<div class="row" align="center">
							<div class="col-md-12 ">
								<img src="../../../static/img/banner_detail/base.png"/>
							</div>
							<div class="col-md-12 fs-36 explain"></div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<m-footer></m-footer>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				index: 1,
			}
		},
	}
</script>

<style scoped>
	.top_banner {
		width: 100%;
		overflow: hidden;
	}
	.top_banner img {
		width: auto;
		margin: 0 50%;
		transform: translateX(-50%);
	}
	.trade .conBox {
		width: 84%;
		margin: 0 8%;
		padding-top: 35px;
	}
	.trade .conBox .con {
		background-image: linear-gradient( #ffffff, #ffffff), linear-gradient( #1d2088, #1d2088);
		background-blend-mode: normal, normal;
		box-shadow: 7px 6px 1px 0px #d8daff;
		border-radius: 10px;
		border: solid 1px #001683;
		padding: 16px 33px;		
	}
	.trade .con .text {
		line-height: 34px;
		margin-bottom: 34px;
	}
	@media only screen and (min-width: 374px) and (max-width: 640px) {
		.trade .conBox .base img{
			width: 100%;
		}
	}	
	.trade .conBox .base{
		padding: 40px 0;
	}
	.trade .conBox .base .explain{
		padding-top:34px;
	}
</style>


